"use client";

import { useRouter } from "next/navigation";
import { 
  Coins, 
  ArrowLeft, 
  Zap, 
  ShieldCheck, 
  Gift, 
  ChevronRight, 
  CheckCircle2,
  TrendingUp,
  ChevronLeft
} from "lucide-react";
import Link from "next/link";

export default function EmpireManual() {
  const router = useRouter();

  return (
    <div className="min-h-screen bg-white font-sans selection:bg-amber-100 flex flex-col">
      
      <nav className="fixed top-0 left-0 right-0 bg-white/90 backdrop-blur-md z-50 border-b border-gray-100 px-6 py-4">
        <div className="max-w-5xl mx-auto flex justify-between items-center">
          <button 
            onClick={() => router.back()} 
            className="flex items-center gap-1 text-gray-900 font-black uppercase text-xs tracking-tighter hover:text-amber-500 transition-colors"
          >
            <ChevronLeft size={20} strokeWidth={3} />
            <span className="hidden md:inline">Back</span>
          </button>
          
          <div className="flex items-center gap-2">
            <span className="font-black text-[10px] md:text-xs uppercase tracking-[0.2em] text-gray-400">Empire Coins</span>
            
          </div>
        </div>
      </nav>

      <main className="flex-1">
        <header className="pt-32 pb-16 px-6 text-center bg-gray-50 rounded-b-[4rem]">
          <div className="max-w-3xl mx-auto">
            <div className="inline-flex items-center gap-2 bg-amber-500/10 text-amber-500 border border-amber-500/20 px-4 py-2 rounded-full mb-4">
              <Gift size={16} />
              <span className="text-[10px] font-black uppercase tracking-[0.2em]">₦50 Welcome Bonus Active</span>
            </div>
            <h1 className="text-4xl md:text-7xl font-black text-gray-900 uppercase tracking-tighter leading-[0.9] mb-6">
              Digital Gold <br />for the <span className="text-amber-500">Hustle.</span>
            </h1>
            <p className="text-gray-500 text-lg md:text-xl font-medium max-w-2xl mx-auto leading-relaxed">
              Empire Coins are more than just points. They are a universal currency designed to reward loyalty across the entire StoreLink network.
            </p>
          </div>
        </header>

        <section className="px-6 -mt-10">
          <div className="max-w-4xl mx-auto bg-gray-900 rounded-[3rem] p-8 md:p-12 shadow-2xl relative overflow-hidden border border-white/5">
             <div className="absolute top-0 right-0 p-10 opacity-20 transform translate-x-10 -translate-y-10">
                <Coins size={200} className="text-amber-500" />
             </div>
             
             <div className="relative z-10 flex flex-col md:flex-row items-center gap-8 text-center md:text-left">
                <div className="flex-1">
                   <h2 className="text-white font-black text-2xl md:text-3xl uppercase tracking-tight mb-4">
                      Claim your <span className="text-amber-500">₦50 Start-Up</span> Capital
                   </h2>
                   <p className="text-gray-400 text-sm mb-6 leading-relaxed">
                      Every new Empire Wallet starts with 50 Coins. Simply sync your WhatsApp number in the gold pill to activate your account.
                   </p>
                   <div className="flex flex-wrap gap-4 justify-center md:justify-start">
                      <div className="bg-white/5 border border-white/10 px-4 py-2 rounded-xl text-white text-[10px] font-black uppercase tracking-widest">
                         1 Coin = ₦1
                      </div>
                      <div className="bg-white/5 border border-white/10 px-4 py-2 rounded-xl text-white text-[10px] font-black uppercase tracking-widest">
                         Instant Use
                      </div>
                   </div>
                </div>
                <div className="bg-amber-500 p-6 rounded-[2rem] text-center shadow-xl shadow-amber-500/20 min-w-[160px]">
                   <p className="text-amber-900 font-black uppercase text-[10px] tracking-widest mb-1">Gift Balance</p>
                   <p className="text-4xl font-black text-white tracking-tighter">₦50.00</p>
                </div>
             </div>
          </div>
        </section>

        <section className="py-24 px-6 max-w-5xl mx-auto">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <div className="space-y-4">
              <div className="w-12 h-12 bg-gray-900 rounded-2xl flex items-center justify-center text-white">
                <Zap size={24} fill="currentColor" />
              </div>
              <h3 className="font-black text-lg uppercase tracking-tight">1. Earn Everywhere</h3>
              <p className="text-gray-500 text-sm leading-relaxed font-medium">
                Shop from any verified StoreLink vendor. Look for the <span className="text-amber-600 font-bold">Empire Badge</span> on products to see exactly how many coins you earn.
              </p>
            </div>

            <div className="space-y-4">
              <div className="w-12 h-12 bg-amber-500 rounded-2xl flex items-center justify-center text-white">
                <TrendingUp size={24} />
              </div>
              <h3 className="font-black text-lg uppercase tracking-tight">2. Grow Your Balance</h3>
              <p className="text-gray-500 text-sm leading-relaxed font-medium">
                Coins are saved to your WhatsApp number. No passwords. Just sync and watch your digital gold grow with every order.
              </p>
            </div>

            <div className="space-y-4">
              <div className="w-12 h-12 bg-emerald-500 rounded-2xl flex items-center justify-center text-white">
                <CheckCircle2 size={24} />
              </div>
              <h3 className="font-black text-lg uppercase tracking-tight">3. Redeem as Cash</h3>
              <p className="text-gray-500 text-sm leading-relaxed font-medium">
                Use your balance to get instant discounts. Your coins spend exactly like Naira at participating stores in the network.
              </p>
            </div>
          </div>
        </section>

        <section className="py-20 px-6 bg-gray-50 rounded-[4rem]">
          <div className="max-w-3xl mx-auto">
            <h2 className="text-3xl font-black text-gray-900 uppercase tracking-tighter text-center mb-12">Common Questions</h2>
            <div className="space-y-4">
              {[
                { q: "Is there a limit to how many coins I can earn?", a: "No. The bigger your hustle, the bigger your Empire. There is no cap on earning." },
                { q: "Do my coins expire?", a: "Never. Your coins are yours forever until you choose to spend them on a discount." },
                { q: "Which stores accept Empire Coins?", a: "All Premium and Diamond StoreLink vendors support the Empire Economy." }
              ].map((item, i) => (
                <div key={i} className="bg-white p-6 rounded-[2rem] border border-gray-100">
                  <div className="font-black text-gray-900 uppercase text-[11px] tracking-widest mb-2 flex items-center gap-2">
                    <div className="w-1.5 h-1.5 bg-amber-500 rounded-full shrink-0" /> {item.q}
                  </div>
                  <p className="text-gray-500 text-sm font-medium leading-relaxed">{item.a}</p>
                </div>
              ))}
            </div>
          </div>
        </section>
      </main>

      <footer className="py-12 px-6 border-t border-gray-100 bg-white">
        <div className="max-w-5xl mx-auto flex flex-col md:flex-row justify-between items-center gap-6">
          <div className="flex items-center gap-2 grayscale opacity-50">
             <div className="bg-gray-900 p-1 rounded text-white">
               <Coins size={12} fill="currentColor" />
             </div>
             <span className="font-black text-[10px] uppercase tracking-widest text-gray-900">StoreLink Empire</span>
          </div>
          
          <div className="flex gap-8 text-[10px] font-black uppercase tracking-widest text-gray-400">
            <Link href="/" className="hover:text-amber-500 transition-colors">Marketplace</Link>
            <Link href="/privacy" className="hover:text-amber-500 transition-colors">Privacy</Link>
            <Link href="/terms" className="hover:text-amber-500 transition-colors">Terms</Link>
          </div>

          <p className="text-[10px] font-bold text-gray-300 uppercase tracking-widest">
            © 2025 StoreLink Inc.
          </p>
        </div>
      </footer>
    </div>
  );
}